<template>
	<div class="about">
		<div class="d-fl justify-space-between mb-4">
			<h1>Корзина</h1>
			<v-btn @click="$router.go(-1)" color="blue lighten-2" flat>
				<v-icon dark left>arrow_back</v-icon>Back
			</v-btn>
		</div>

		<div v-if="offers.length" class="cart">
			<p v-for="offer in offers" :key="offer.id">
				<span @click="$store.commit('storage/CHANGE_QUANTITY', {add: false, id: offer.id})">-</span>
				{{ offer.name }}
				<span
					@click="$store.commit('storage/CHANGE_QUANTITY', {add: true, id: offer.id})"
				>+</span>
				-- {{ offer.quantity }} ------
				<span
					@click="$store.commit('storage/REMOVE_FROM_CART', offer.id)"
				>X</span>
			</p>
			<p @click="$store.commit('storage/CLEAR', {storage: 'cart'})">Удалить всё</p>
		</div>

		<v-layout v-else mt-5>
			<v-flex class="text-xs-center">К сожалению ничего не нашлось</v-flex>
		</v-layout>
	</div>
</template>


<script>
export default {
	computed: {
		offers() {
			return this.$store.state.storage.cart;
		}
	}
};
</script>
