import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store/index';

import VueToastr2 from 'vue-toastr-2';
import 'vue-toastr-2/dist/vue-toastr-2.min.css';

window.toastr = require('toastr');

Vue.use(VueToastr2);

import Vuetify from 'vuetify';
// import 'vuetify/dist/vuetify.min.css';
Vue.use(Vuetify, {
	theme: {
		primary: '#1976D2',
		secondary: '#424242',
		accent: '#82B1FF',
		error: '#FF5252',
		info: '#2196F3',
		success: '#4CAF50',
		warning: '#FFC107'
	}
});

Vue.filter('priceFormat', value =>
	value ? parseInt(value).toLocaleString('ru-RU') : ''
);

Vue.config.productionTip = false;

new Vue({
	beforeCreate() {
		this.$store.dispatch('storage/getOffers', { storage: 'cart' });
		this.$store.dispatch('storage/getOffers', { storage: 'favorites' });
		this.$store.dispatch('storage/getActiveTypeId');
		// this.$store.dispatch('storage/getPage');
	},
	router,
	store,
	render: h => h(App)
}).$mount('#app');
